import { useState } from "react";
import { makeStyles, Typography, Button, Box } from "@material-ui/core";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import { PageWrapper } from "components";
import { LeagueSection } from "utils/enums";
import { LeagueSectionContent } from "./components";

const useStyles = makeStyles((theme) => ({
  content: {
    textAlign: "center",
    position: "relative",
  },
  tabsWrapper: {
    marginBottom: 16,
    [theme.breakpoints.down("md")]: {
      marginBottom: 0,
    },
  },
  tabsContainer: {
    width: "100%",
    textAlign: "center",
    margin: "10px auto",
    padding: "1rem",
    [theme.breakpoints.down("md")]: {
      padding: 0,
    },
  },
  tabsItem: {
    backgroundColor: theme.colors.neutralLight,
    color: theme.colors.primary,
  },
  indicator: {
    backgroundColor: theme.colors.primary,
  },
  button: {
    backgroundColor: theme.colors.primary,
    outline: "none",
    border: 0,
    marginTop: 12,
  },
}));
const dataTabs = [
  {
    id: 0,
    mode: LeagueSection.Ended,
  },
  {
    id: 1,
    mode: LeagueSection.Ongoing,
  },
  {
    id: 2,
    mode: LeagueSection.Upcoming,
  },
  {
    id: 3,
    mode: LeagueSection.Finalized,
  },
];
const LeaguePage = () => {
  const classes = useStyles();
  const [value, setValue] = useState(2);// set default value to 'UPCOMING'
  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };

  return (
    <PageWrapper>
      <div className={classes.content}>
        <Typography align="center" component="h3" variant="h1">
          Prediction Pools
        </Typography>
        <Box className={classes.tabsWrapper}>
          <Tabs
            value={value}
            onChange={handleChange}
            className={classes.tabsContainer}
            centered
            TabIndicatorProps={{ className: classes.indicator }}
          >
            {dataTabs.map((item, i) => {
              return (
                <Tab
                  label={item.mode}
                  className={classes.tabsItem}
                  disableRipple
                  key={i}
                />
              );
            })}
          </Tabs>
        </Box>
        {dataTabs.map((item) => {
          return (
            <LeagueSectionContent
              mode={item.mode}
              value={value}
              index={item.id}
            />
          );
        })}
      </div>
    </PageWrapper>
  );
};

export default LeaguePage;
