import { Grid, makeStyles, Typography, Box } from "@material-ui/core";
import { SimpleLoader } from "components";
import { useLeagues } from "helpers";
import { LeagueSection } from "utils/enums";
import { LeaguePreview } from "../LeaguePreview";
import { Pagination } from "../Pagination";
const useStyles = makeStyles((theme) => ({
  root: {
    "&+&": {
      marginTop: 24,
    },
  },
  content: { margin: "16px 0", textAlign: "left" },
  noIdo: { ontSize: 18, color: theme.colors.neutralLighter, margin: "24px 0" },
}));

interface IProps {
  mode: LeagueSection;
}
interface TabsProps {
  onClick?: () => void;
  children?: React.ReactNode;
  value: number;
  index: number;
}
function TabPanel(props: TabsProps) {
  const { children, value, index, ...other } = props;
  return <div {...other}>{value === index && <Box p={3}>{children}</Box>}</div>;
}

export const LeagueSectionContent = (props: IProps & TabsProps) => {
  const classes = useStyles();
  const { mode, value, index } = props;
  const { loading, data, loadMore, hasMore } = useLeagues(mode);

  return (
    <TabPanel value={value} index={index}>
      <div className={classes.root}>
        <div className={classes.content}>
          {data.length > 0 && (
            <Grid container spacing={2}>
              {data.map((league) => (
                <Grid item key={league.address} xs={12} sm={6} md={4}>
                  <LeaguePreview id={league.address} />
                </Grid>
              ))}
            </Grid>
          )}
          {loading && <SimpleLoader />}
          {data.length === 0 && !loading && (
            <Typography align="center" className={classes.noIdo}>
              No {mode} League
            </Typography>
          )}
        </div>
        <Pagination
          total={data.length}
          disabled={loading || !hasMore}
          loadMore={loadMore}
        />
      </div>
    </TabPanel>
  );
};
