import { makeStyles, Typography } from "@material-ui/core";
import { LazyImage, SimpleLoader, CountDownTimer } from "components";
import { getTokenFromAddress } from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { useLeague } from "helpers";
import { transparentize } from "polished";
import React, { useState } from "react";
import { formatBigNumber, numberWithCommas } from "utils";
import { getLeagueStatus } from "utils/ido";
import KeyboardArrowRightIcon from "@material-ui/icons/KeyboardArrowRight";
import { LeagueSection } from "utils/enums";
import clsx from "clsx";
import { NavLink } from "react-router-dom";
import { IToken } from "types";
import moment from "moment-timezone";

const AVATAR_WIDTH = 100;

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: AVATAR_WIDTH / 2,
    position: "relative",
    margin: "0 5px 16px 5px",
  },
  content: {
    paddingTop: AVATAR_WIDTH / 2,
    backgroundColor: theme.colors.neutralLight,
  },
  avatarWrapper: {
    position: "absolute",
    top: -AVATAR_WIDTH / 2,
    left: `calc(50% - ${AVATAR_WIDTH / 2}px)`,
  },
  avatar: {
    width: AVATAR_WIDTH,
    height: AVATAR_WIDTH,
    borderRadius: "50%",
  },

  main: { padding: 16 },
  header: {
    display: "flex",
    alignItems: "flex-start",
    marginBottom: 16,
  },
  title: {
    color: theme.colors.neutralLighter,
    fontWeight: 600,
    fontSize: 24,
    marginRight: 8,
  },
  symbol: {
    color: theme.colors.neutralDark,
    fontSize: 14,
    backgroundColor: transparentize(0.2, theme.colors.neutralLighter),
    padding: "4px 10px",
    marginTop: 12,
    fontWeight: 700,
    letterSpacing: 1,
  },
  group: {
    "&+&": {
      marginTop: 12,
    },
  },
  label: { color: theme.colors.neutralLighter, fontWeight: 500, fontSize: 12 },
  value: { color: theme.colors.neutralLighter, fontWeight: 600, fontSize: 18 },
  linkWrapper: {
    display: "flex",
    justifyContent: "center",
    marginTop: 16,
  },
  link: {
    display: "inline-flex",
    color: theme.colors.primary,
    alignItems: "center",
    justifyContent: "center",
    position: "relative",
    textDecoration: "none",
    padding: "8px 12px",
    "&:hover": {
      textDecoration: "underline",
    },
  },
  statusLabel: {
    position: "absolute",
    top: 0,
    right: 0,
    padding: "4px 6px",
    border: `1px solid ${theme.colors.transparent}`,
    fontWeight: 600,
    fontSize: 14,
    "&.upcoming": {
      backgroundColor: transparentize(0.9, theme.colors.green),
      color: theme.colors.green,
    },
    "&.ongoing": {
      backgroundColor: transparentize(0.9, theme.colors.primary),
      color: theme.colors.primary,
    },
    "&.ended": {
      backgroundColor: transparentize(0.9, theme.colors.error),
      color: theme.colors.error,
    },
    "&.finalized": {
      color: theme.colors.blue,
      borderColor: theme.colors.blue,
    },
  },
  progressWrapper: {
    position: "relative",
    backgroundColor: theme.colors.neutralLight,
    marginBottom: 16,
    marginTop: 16,
    height: 30,
    borderRadius: 16,
    color: theme.colors.neutralLighter,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    fontSize: 14,
    fontWeight: 600,
    border: `1px solid ${transparentize(0.8, theme.colors.neutralLighter)}`,
    overflow: "hidden",
    "& span": {
      zIndex: 2,
    },
  },
  progress: {
    height: 30,
    position: "absolute",
    left: 0,
    top: 0,
    bottom: 0,
    backgroundColor: theme.colors.primary,
  },
}));

interface IProps {
  id: string;
}

export const LeaguePreview = (props: IProps) => {
  const { data } = useLeague(props.id);
  const classes = useStyles();
  const [counter, setCounter] = useState(1);
  const { networkId } = useConnectedWeb3Context();
  const increateCounter = () => setCounter((counter) => counter + 1);

  const renderContent = () => {
    if (!data || !data.meta) return null;
    const { startTime, closeTime } = data;
    const status = getLeagueStatus(data);
    let fundToken: IToken | null = null;

    try {
      fundToken = getTokenFromAddress(data.token, networkId);
    } catch (error) {}

    if (!fundToken) return null;

    return (
      <>
        <div className={classes.main}>
          <div className={classes.header}>
            <Typography className={classes.title}>
              {data.metaData.title}
            </Typography>
          </div>
          <Typography
            className={clsx(
              classes.statusLabel,
              status === LeagueSection.Upcoming && "upcoming",
              status === LeagueSection.Ongoing && "ongoing",
              status === LeagueSection.Ended && "ended",
              status === LeagueSection.Finalized && "finalized"
            )}
          >
            {status === LeagueSection.Upcoming && "Coming soon"}
            {status === LeagueSection.Ongoing && "Live"}
            {status === LeagueSection.Ended && "Ended"}
            {status === LeagueSection.Finalized && "Finalized"}
          </Typography>
          <div className={classes.group}>
            <Typography className={classes.label}>
              {status === LeagueSection.Upcoming && "League starts"}
              {status === LeagueSection.Ongoing && "League ends"}
              {status === LeagueSection.Ended && "League Ended"}
              {status === LeagueSection.Finalized && "League Finalized"}
            </Typography>

            {status === LeagueSection.Upcoming && (
              <CountDownTimer
                destTimeStamp={startTime * 1000}
                onReached={increateCounter}
              />
            )}
            {status === LeagueSection.Ongoing && (
              <CountDownTimer
                destTimeStamp={closeTime * 1000}
                onReached={increateCounter}
              />
            )}
          </div>
          <div className={classes.group}>
            <Typography className={classes.label}>Start Time</Typography>
            <Typography className={classes.value}>
              {moment(new Date(startTime * 1000))
                .utc()
                .format("MM/DD/YYYY HH:mm")}{" "}
              GMT
            </Typography>
          </div>
          <div className={classes.group}>
            <Typography className={classes.label}>Close Time</Typography>
            <Typography className={classes.value}>
              {moment(new Date(closeTime * 1000))
                .utc()
                .format("MM/DD/YYYY HH:mm")}{" "}
              GMT
            </Typography>
          </div>
          <div className={classes.group}>
            <Typography className={classes.label}>Total Entry</Typography>
            <Typography className={classes.value}>
              {data.currentChoiceId.toNumber()}
            </Typography>
          </div>

          <div className={classes.group}>
            <Typography className={classes.label}>Entry Price</Typography>
            <Typography className={classes.value}>
              {numberWithCommas(
                formatBigNumber(data.entryAmount, fundToken.decimals, 0)
              )}{" "}
              {fundToken.symbol.toUpperCase()}
            </Typography>
          </div>
          <div className={classes.group}>
            <Typography className={classes.label}>Prize Amount</Typography>
            <Typography className={classes.value}>
              {numberWithCommas(
                formatBigNumber(data.prizeAmount, fundToken.decimals, 0)
              )}{" "}
              {fundToken.symbol.toUpperCase()}
            </Typography>
          </div>
          <div className={classes.linkWrapper}>
            <NavLink
              className={classes.link}
              to={`/prediction-pools/${data.address}`}
            >
              Research
              <KeyboardArrowRightIcon />
            </NavLink>
          </div>
        </div>
      </>
    );
  };

  return (
    <div className={classes.root}>
      {!data ? null : (
        <div className={classes.avatarWrapper}>
          <LazyImage
            src={data.metaData.banner}
            className={classes.avatar}
            imgClassName={classes.avatar}
          />
        </div>
      )}
      <div className={classes.content}>
        {!data ? <SimpleLoader /> : renderContent()}
      </div>
    </div>
  );
};
